.mixed-letters-wrapper {
  margin-bottom: 15px;
  height: 150px;

  color: #2b323a;
  font-size: 35px;
  text-align: center;

  user-select: none;
}

.mixed-letters-wrapper span {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin: 30px 5px;

  border: 2px solid #2b323a;

  cursor: pointer;
}
