@keyframes start-picture-animation {
  50% {background-position-y: 15px}
  100% {background-position-y: 0}
}

.battle-user-hero {
  width: 400px;
  height: 470px;
}

@keyframes ironman-hit {
  80% {background-position-x: -1600px; width: 400px}
  99% {background-position-x: -1600px; width: 1600px}
  100% {background-position-x: -1600px; width: 400px}
}

@keyframes ironman-hit-slide {
  99% {margin-left: 30%}
  100% {margin-left: 0}
}

.battle-ironman-firstType-hit,
.battle-ironman-secondType-hit {
  animation: ironman-hit 1.5s steps(4), ironman-hit-slide 1.5s steps(100);
}

@keyframes hulk-hit {
  80% {background-position-x: -1600px}
  100% {background-position-x: -1600px}
}

@keyframes hulk-secondType-hit {
  50% {background-position-x: -1600px; width: 400px}
  99% {background-position-x: -1600px; width: 2000px}
  100% {background-position-x: -1600px; width: 400px}
}

.battle-hulk-firstType-hit {
  animation: hulk-hit 1.5s steps(4);
}

.battle-hulk-secondType-hit {
  animation: hulk-secondType-hit 1.5s steps(4);
}

@keyframes spiderman-hit {
  50% {background-position-x: -800px; width: 400px}
  99% {background-position-x: -800px; width: 2000px}
  100% {background-position-x: -800px; width: 400px}
}

@keyframes spiderman-secondType-hit {
  80% {background-position-x: -1600px}
  100% {background-position-x: -1600px}
}

@keyframes spiderman-hit-slide {
  20% {margin-left: 0}
  99% {margin-left: 60%}
  100% {margin-left: 0}
}

.battle-spiderman-firstType-hit {
  animation: spiderman-hit 1.5s steps(2);
}

.battle-spiderman-secondType-hit {
  animation: spiderman-secondType-hit 1.5s steps(4), spiderman-hit-slide 1.5s steps(100);
}

@keyframes blackpanther-hit {
  80% {background-position-x: -2400px}
  100% {background-position-x: -2400px}
}

@keyframes blackpanther-secondType-hit {
  80% {background-position-x: -1600px}
  100% {background-position-x: -1600px}
}

@keyframes blackpanther-hit-slide {
  20% {margin-left: 0}
  99% {margin-left: 60%}
  100% {margin-left: 0}
}

.battle-blackpanther-firstType-hit {
  animation: blackpanther-hit 1.5s steps(6), blackpanther-hit-slide 1.5s steps(100);
}

.battle-blackpanther-secondType-hit {
  animation: blackpanther-secondType-hit 1.5s steps(4), blackpanther-hit-slide 1.5s steps(100);
}

@keyframes hero-killed {
  1% {background-position-y: -465px; background-position-x: 0}
  80% {background-position-y: -465px; background-position-x: -1600px}
  100% {background-position-y: -465px; background-position-x: -1600px}
}

.battle-hero-killed {
  animation: hero-killed 1.6s steps(4);
}

@keyframes hero-shaked {
  50% {background-position-y: -470px}
  100% {background-position-y: -470px}
}

.battle-hero-shaked {
  animation: hero-shaked 0.6s steps(1);
}
