@keyframes show-modal {
  0% {
    transform: translateY(-1000px);
  }

  100% {
    transform: translateY(0);
  }
}

.modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 700px;
  height: 400px;
  margin-top: -200px;
  margin-left: -350px;

  background-color: #ffffff;
  border-radius: 50px;

  z-index: 10;
}

.modal h2{
  margin: 25px 0;
  margin-bottom: 35px;

  color: #2b323a;
  font-size: 40px;
  text-align: center;

  user-select: none;
}

.modal-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0,0,0,0.5);
  z-index: 9;
}

.modal-show {
  display: block;
  animation: show-modal 0.5s;
}

.hit-type-wrapper {
  display: flex;
  justify-content: center;
  width: 90%;
  margin: 0 auto;

  font-size: 25px;
}

.hit-type {
  box-sizing: border-box;
  width: 200px;
  margin: 0 35px;
  padding: 15px;

  text-align: center;

  border: 2px solid #2b323a;
  border-radius: 10px;

  list-style: none;
  user-select: none;
  cursor: pointer;
}

.hit-type:hover,
.hit-type:focus {
  border-color: #ff0000;
  outline: none;
}

.hit-type li{
  margin-bottom: 15px;
}

.hit-type h3 {
  margin: 0;
}

.hit-type span {
  display: block;
  font-weight: bold;
}

.modal-hit-type-close {
  position: absolute;
  top: 30px;
  right: 50px;
  width: 30px;
  height: 30px;

  font-size: 0;

  background-color: #ffffff;
  border: none;

  cursor: pointer;
}

.modal-hit-type-close::before,
.modal-hit-type-close::after {
  content: "";

  position: absolute;
  top: 14px;
  right: 0;

  width: 30px;
  height: 3px;

  background-color: #2b323a;
}

.modal-hit-type-close::before {
  transform: rotate(45deg);
}

.modal-hit-type-close::after {
  transform: rotate(-45deg);
}

.modal-hit-type-close:hover,
.modal-hit-type-close:focus{
  outline: 1px solid #ff0000;
}
