.modal-task h2 {
  margin: 15px 0;
  padding: 0;
}

.current-task-label {
  display: block;
  margin-bottom: 10px;

  color: #2b323a;
  font-size: 40px;
  text-align: center;
}

.task-user-answer {
  display: block;
  width: 60%;
  height: 40px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0 15px;

  font: inherit;
  font-size: 30px;

  border: 2px solid #2b323a;
}

.task-user-answer:focus {
  border-color: #ff0000;
  outline: none;
}


.task-answer-button {
  display: block;
  width: 250px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 10px;

  font: inherit;
  color: #ffffff;
  font-size: 30px;

  background-color: #2b323a;
  border: 2px solid #2b323a;
  border-radius: 5px;

  user-select: none;
  cursor: pointer;
}
.task-answer-button:hover,
.task-answer-button:focus {
  border-color: #ff0000;
  outline: none;
}

.modal-task-right-answer {
  border: 3px solid #7cfc00;
}

.modal-task-right-answer::before,
.modal-task-right-answer::after {
  content: "";

  position: absolute;
  top: 75%;
  left: 75%;

  width: 20%;
  height: 3px;

  background-color: #7cfc00;
}

.modal-task-right-answer::before {
  transform: rotate(90deg);
}

.modal-task-wrong-answer {
  border: 3px solid #ff0000;
}

.modal-task-wrong-answer::before,
.modal-task-wrong-answer::after {
  content: "";

  position: absolute;
  top: 50%;
  left: 15%;

  width: 70%;
  height: 3px;

  background-color: #ff0000;
}

.modal-task-wrong-answer::before {
  transform: rotate(45deg);
}

.modal-task-wrong-answer::after {
  transform: rotate(-45deg);
}
