.question-task-wrapper {
  margin-bottom: 15px;
  padding: 15px 0;

  color: #2b323a;
  font-size: 30px;
  text-align: center;

  user-select: none;
}

.question-task-wrapper span {
  display: inline-block;
  height: 50px;
  margin: 10px;
  padding: 0 10px;

  border: 2px solid #2b323a;

  cursor: pointer;
}

.question-task-wrapper span:hover,
.question-task-wrapper span:focus,
span.question-task-letter-checked {
  border: 2px solid #ff0000;
  outline: none;
}
