.hero-start {
  width: 250px;
  height: 291px;
}

.battle-monster {
  position: relative;
  width: 400px;
  height: 470px;
}

.monster-head {
  position: absolute;
  left: 30%;
  top: 17%;
  width: 70px;
  height: 70px;
  z-index: 3;
}

.monster-body {
  position: absolute;
  left: 26%;
  top: 23%;
  width: 120px;
  height: 185px;
  z-index: 2;
}

.monster-right-hand {
  position: absolute;
  left: 4%;
  top: 27%;
  width: 135px;
  height: 125px;
  z-index: 1;
}

.monster-right-leg {
  position: absolute;
  left: 8%;
  top: 49%;
  width: 135px;
  height: 150px;
  z-index: 1;
}

.monster-left-hand {
  position: absolute;
  left: 40%;
  top: 25%;
  width: 110px;
  height: 140px;
  z-index: 3;
}

.monster-left-leg {
  position: absolute;
  left: 33%;
  top: 50%;
  width: 120px;
  height: 240px;
  z-index: 3;
}


@keyframes monster-head-shake {
  50% {top: 12%}
  100% {top: 17%}
}

@keyframes monster-left-hand-shake {
  50% {left: 45%}
  100% {left: 40%}
}

@keyframes monster-left-leg-shake {
  50% {left: 40%}
  100% {left: 33%}
}

@keyframes monster-right-hand-shake {
  50% {left: 0}
  100% {left: 4%}
}

@keyframes monster-right-leg-shake {
  50% {left: 4%}
  100% {left: 8%}
}

.battle-monster-head-shaked {
  animation: monster-head-shake 0.5s steps(100);
}

.battle-monster-left-hand-shaked {
  animation: monster-left-hand-shake 0.5s steps(100);
}

.battle-monster-left-leg-shaked {
  animation: monster-left-leg-shake 0.5s steps(100);
}

.battle-monster-right-hand-shaked {
  animation: monster-right-hand-shake 0.5s steps(100);
}

.battle-monster-right-leg-shaked {
  animation: monster-right-leg-shake 0.5s steps(100);
}

@keyframes monster-head-kill {
  100% {transform: rotate(90deg); top: 65%}
}

@keyframes monster-body-kill {
  100% {transform: rotate(90deg); top: 65%}
}

@keyframes monster-left-hand-kill {
  100% {transform: rotate(90deg); top: 65%}
}

@keyframes monster-left-leg-kill {
  100% {transform: rotate(90deg); top: 62%}
}

@keyframes monster-right-hand-kill {
  100% {transform: rotate(90deg); top: 65%}
}

@keyframes monster-right-leg-kill {
  100% {transform: rotate(90deg); top: 60%}
}

.battle-monster-head-kill {
  animation: monster-head-kill 1s steps(100);
}

.battle-monster-body-kill {
  animation: monster-body-kill 1s steps(100);
}

.battle-monster-left-hand-kill {
  animation: monster-left-hand-kill 1s steps(100);
}

.battle-monster-left-leg-kill {
  animation: monster-left-leg-kill 1s steps(100);
}

.battle-monster-right-hand-kill {
  animation: monster-right-hand-kill 1s steps(100);
}

.battle-monster-right-leg-kill {
  animation: monster-right-leg-kill 1s steps(100);
}

@keyframes shake {
  0% { transform: translate(2px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(0px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(2px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(2px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

.battle-monster-hit {
  animation: shake 1.5s;
}
