body {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.fight-wrapper {
  position: relative;
  width: 95vw;
  height: 90vh;
  margin: 0 auto;

  background-color: #2b323a;
}

.health-wrapper {
  display: flex;
  justify-content: space-between;
  margin: 30px 0;
  padding: 0 30px;

  user-select: none;
}

.health {
  width: 45%;
  height: 100px;

  border-radius: 15px;
  background-color: #ffffff;
}

.health-user {
  border-bottom-right-radius: 100px;
}

.health-monster {
  border-bottom-left-radius: 100px;
}

.round-wrapper p {
  margin: 0;
  padding: 0 15px;

  color: #2b323a;
  font-size: 40px;
  text-align: center;

  white-space: nowrap;
}
.round-wrapper .versus {
  margin: 0;

  color: #2b323a;
  font-size: 50px;
}

.health-name {
  margin: 10px 0;

  text-align: center;
  text-transform: uppercase;
  font-size: 30px;

  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.health-ph-wrapper {
  width: 70%;
  height: 25px;
  margin: 0 auto;

  border: 2px solid #2b323a;
}

.health-ph {
  height: 100%;
  background-color: #2b323a;
}

.health-monster-ph {
  margin-left: auto;
}

.action-button {
  display: block;
  width: 350px;
  margin: 0 auto;
  margin-bottom: 100px;
  padding: 10px;

  font: inherit;
  color: #2b323a;
  font-size: 30px;

  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 5px;

  user-select: none;
  cursor: pointer;
}
.action-button:hover,
.action-button:focus {
  border-color: #ff0000;
  outline: none;
}

.battle-fight-wrapper {
  position: absolute;
  z-index: -1;
  left: 50%;
  bottom: 1%;
  display: flex;
  justify-content: space-between;
  width: 1500px;
  margin-left: -750px;
}
